.header {
    width: 100%;
    display: flex;
    padding: 0px 20px 20px 20px;
    background: transparent;
    position: absolute;
    position: fixed;
    justify-content: space-between;
    align-items: center;
    transition: background-color 0.5s ease;
}
.header.scrolled {
    background-color: #000000de;
    max-height: 60px;
    z-index: 1000;  
}
.header-image {
    width: 45%;
}
.header-button {
    width: 50%;
}
.header-image h1 {
    font-family: 'Brush Script MT', 'Lucida Handwriting', cursive;
    color: #cdedfb;
    font-size: 60px;
    
}
.header-button {
    padding: 27px 50px 25px 25px;
    border-radius: 50px; 
    transition: all 0.5s ease;
    
 }
.header-button div {
    padding: 2%;
    border: 2px solid rgb(195 233 250);
    border-radius: 50px !important;
    float: right;
    color: aliceblue;
    background: linear-gradient(to right, white, rgb(117, 205, 243)); 
    -webkit-background-clip: text; 
    color: transparent;
}
.header-image h1.smaller {
    font-size: 35px; 
    margin-top: 40px;
    transition: 1s ease;
}
.header-button.smaller {
    font-size: 13px;
    width: 31%;
    margin-top: 10px;
    padding: 5px 50px 0px 0px; 
    transition: 1s ease;
}