.contact-section {
    background-image: url('../images/conatctBackground.jpg');
    background-size: 100% auto;
    min-height: 40vh;
}
.contact-content{
    background: #00000078;
    display: flex;
    min-height: 40vh;
    align-items: center;
}
.contact-details {
    color: #fff;
    width: 35%;
    text-align: right;
    padding: 55px;
}
.conatct-logo{
    width: 35%;
}
.contact-links{
    width: 30%;
}
.contact-links ul{
    list-style: none;
}
.contact-links li {
    display: contents;
    margin-left: 20px;
}
.contact-copyright {
    background-color: #000;
    color: #fff;
    text-align: center;
    min-height: 45px;
}
.copyright-para {
    margin: 0;
    padding: 10px;
}