.progress {
    width: 100%;
    background-color: #f0f0f0;
    height: 8px; 
    border-radius: 4px;
    margin-bottom: 10px;
  }
  
  .progress-bar {
    width: 80%;
    height: 100%;
    background-color: #7cd0f4;
    border-radius: 4px;
    transition: width 1s ease;
  }

  .about-me {
    text-align: center;
  }

  .content-row {
    display: flex;
    width: 100%;
    padding: 35px 0px 9px 0px;
  }
  
  .content-detail {
    width: 40%;
    padding: 50px;
  }

  .content-skills {
    width: 50%;
    padding: 50px;
  }

  
  .content-skills ul {
    list-style: none;
  }

  .content-skills li {
    line-height: 32px;
  }

  .skill-percentage {
    float: right;
  } 

  .content-detail ul {
    list-style: none;
    padding: 30px 0px 0px 40px;
    line-height: 2.2;
  }