

.project-section {
    display: flex;
    width: 100%;
    background-color: #f6f6f6;
}
.project-left-section {
    width: 30%;
    padding: 50px;
    margin-top: 6%;
}
.next-line {
    display: block;
    margin-top: 5px;
    font-size: 28px;
}
.project-images {
    width: 70%;
    padding: 11% 20px 20px 20px;
}
.project-images img{
    width: 17vw;
    min-height: 50vh;
    padding: 20px;
}
.project-list ul {
    list-style: none;
    line-height: 40px;
    width: 147px;
}
.project-list li:hover {
    border: 2px solid black;
    text-align: center;
    /* padding: 2px; */
    border-left: none;
    border-right: none;
    cursor: pointer;
}
.explore-arrow img{
    margin-top: 30px;
    transform: rotateZ(30deg);
    min-height: 25vh !important;
}
.explore-arrow h1 {
    margin-top: 100px;
}
.project-discover {
    font-size: 28px;
}

.image-title {
    position: absolute;
    margin-left: 20px;
    min-height: 10vh;
    color: #f6f6f6;
    width: 15vw;
    background-color: hsla(0, 0%, 0%, 0.393);
    text-align: center;
    padding-top: 6px;
    margin-top: 20px;
}
.image-container {
    display: flex;
}
.coming-soon {
    background-image: url('../images/comingsoonbackground.webp');
    min-height: 70vh;
    width: 100%;
    background-attachment: fixed;
    background-size: contain;
    margin-bottom: 20%;
    border-radius: 50%;
    text-align: center;
}
.coming-soon h1 {
    color: #f6f6f6;
    font-family: Brush Script MT, Brush Script Std, cursive;
    padding: 28%;
    font-size: 3rem;
}