
.body-caption {
    position: absolute;
    color: aliceblue;
    margin-left: 5%;
}
.body-caption h3 {
    font-size: 42px;
    letter-spacing: 8px;
    font-family: Papyrus, fantasy;
    font-variant: small-caps;
    margin-bottom: 0 !important;
    background: linear-gradient(to right, white, rgb(117, 205, 243)); 
    -webkit-background-clip: text; 
    color: transparent;
    margin-top: 0;
}
.body-caption .body-title {
    display: flex;
    letter-spacing: 9px;
    font-size: 23px;
    margin-top: 0 !important;
    font-weight: 600;
    line-height: 80px;
    font-family:  "Courier", monospace;
}
.body-title {
    background: linear-gradient(to right, white, rgb(117, 205, 243)); 
    -webkit-background-clip: text; 
    color: transparent;
}
.body {
    display: grid;
    width: 100%;
    text-align: center;
    align-items: center ;
}